.mywork{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 65px;
    margin: 60px 150px;
}
.mywork-title{
    position: relative;
}
.mywork-title h1{
    padding: 0px 20px;
    font-size: 65px;
    font-weight: 500;
}
.mywork-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
}
.mywork-container img{
    box-sizing: border-box;
    width: 400px;
    height: 250px;
    transition: 0.3s;
    cursor: pointer;
}
.mywork-container img:hover{
    transform: scale(1.10);
    border: 4px solid #ff00ff;
    border-radius: 10px;
    transition: 0.3s;
}
.mywork-showmore{
    display: flex;
    gap: 15px;
    border-radius: 50px;
    border: 2px solid white;
    padding: 10px 20px;
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 65px;
    transition: 0.6s;
    cursor: pointer;
}
.mywork-showmore img{
    margin-top: 8px;
    width: 40px;
    height: 15px;
    background: linear-gradient(270deg, #DF8908 10%, #B415FF 100%);
}
.mywork-showmore:hover{
    gap: 30px;
    transition: 0.6s;
}
@media(max-width:768px) {
    .mywork{
        gap: 50px;
        margin: 45px 65px;
        align-items: start;
    }
    .mywork-title h1{
        padding: 0;
        font-size: 45px;
        font-weight: 550;
    }
    .mywork-container{
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }
    .mywork-container img{
        width: 100%;
        height: 100%;
    }
    .mywork-showmore{
        padding: 15px 35px;
        font-size: 16px;
        margin: 45px auto;
        cursor: pointer;
    }
}